import React, { Component  } from 'react';
import { StaticQuery, graphql } from "gatsby";
import Navbar from '../components/navbar';
import Contact from '../components/contact';
import Footer from '../components/footer';
import Download from "../images/download.webp";


export default function Thanks () {
  
  return (
    <StaticQuery
      query = {graphql`
      query Thanks {
          allWpPage(filter: {slug: {eq: "thank-you"}}) {
            nodes {
              content
            }
          }
        }
      `}
      render = { data => (
        
        <><Navbar /><section id="post-202" class="post-202 page type-page status-publish hentry">
          <header class="entry-header text-center" style={{ marginTop: "100px" }}><h1 class="entry-title mb-4">Thank You</h1></header>
          <div class="entry-content">
            <div class="container">
              <div class="row text-center">
                <div dangerouslySetInnerHTML={{ __html: data.allWpPage.nodes[0].content }} />
              </div>
            </div>
          </div>
        </section><Contact/><Footer /></>

      )}
    />
  )
}